#root {
  font-weight: bold;
  font-family: "Suez One", serif;
  font-weight: 400;
  font-style: normal;
}
h1 {
  font-size: 12vw;
  margin: 0;
}

.arrow-box {
  position: relative;
  display: flex;
  border-bottom: 6px solid black;
  margin-bottom: 10px;
  margin-top: 5px;
}

.arrow-box::after {
  content: '';
  position: absolute;
  top: 104%;
  left: 100%;
  transform: translateY(-50%);
  border-width: 10px 0 10px 15px;
  border-style: solid;
  border-color: transparent transparent transparent #000000;
}
